.friendWrapper div {
    color: white;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.friendWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.friendWrapper a.active {
    color: rgb(129, 123, 123);
}

.friendAvatar {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    transition: .4s all;
    border: 4px double white;
}

.friendAvatar:hover {
    transform: rotate(20deg);
}