.nav {
    grid-area: n;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 20px;
}

.shortcutsWrapper {
    background-color: rgb(46, 10, 7);
    padding: 20px 60px 20px 10px;
    font-size: 25px;
    border: 2px solid rgb(36, 9, 7);
    border-radius: 20px;

}

.item {
    padding: 4px;
}

.item a {
    color: white;
    text-decoration: none;
    transition: .40s all;

}

.item a.active {
    color: rgb(129, 123, 123);
    text-decoration: underline;
    font-weight: bold;

}

.item a:hover {
    letter-spacing: 1.5px;
    text-decoration: none;
    transform: scale(1, 1);
    color: rgb(172, 157, 157);
    display: block;
    font-weight: 600;
}



.sidebarFriends {
    color: white;
    font-size: 26px;
    text-decoration: none;
    background-color: rgb(46, 10, 7);
    border: 2px solid rgb(36, 9, 7);
    border-radius: 20px;
    padding: 20px;
}

.friendsWrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
}

.friends {
    display: flex;
    justify-content: center;
    align-items: center;
    color:rgb(129, 123, 123);
    font-weight: 600;
    border-bottom: thick double rgb(121, 113, 113);
    font-family: 'Courier New', Courier, monospace;
}
