.header {
    grid-area: h;
    background-color: rgb(46, 10, 7);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
}

.titleWrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header img {
    width: 50px;
}

.title {
    color: rgb(177, 83, 54);
    padding: 4px;
    font-size: 25px;
    font-weight: bold;
}

.loginBlock {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: white;
}

.loginBlock a {
    color: white;
}