.app-wrapper {
  display: grid;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: rgba(48, 12, 12, 0.76);

  grid-template-areas: 
  "h h"
  "n c";
  grid-template-rows: 80px 1fr;
  grid-template-columns: 3fr 8fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(46, 10, 7);
  margin: 20px;
  border: 2px solid rgb(36, 9, 7);
  border-radius: 20px;
  color: white;
  max-width: 1024px;
}


